* {
 box-sizing: border-box;
 padding: 0px;
 margin: 0px; 
}
body {
  text-align: center;
  padding: 15px;
  background-color: #f3f7fa;
}
.container-fluid > div:first-child {
  padding: 5%;
}
section > p {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  
}
.filter {
  display: grid;
  grid-template-columns: 1fr 3fr; 
  grid-template-rows: repeat(5, 1fr); 
  text-align: left;
  
}









.tickets {
  list-style: none;
}
.tickets li {
  display: flex;
  padding: 0px;
}
.thumbnail {
  display: flex;
  flex-direction: row;
}
.tickets__btn-buy-wrapper {
  align-items: flex-start;
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
  padding: 15px;
  max-width: 200px;
}
.tickets__btn-buy-wrapper > p {
  font-family: fantasy;
  letter-spacing: 3px;
  font-size: 17px;
  color: rgb(0, 0, 151);
  line-height: 17px;
  padding: 1px; 
}
.btn-success {
  margin-top: 10px;
  background-color: rgb(255, 101, 45);
  border: 1px solid rgb(253, 68, 0);
  width: 150px;
}
.tickets__info-wrapper {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  padding: 15px;
  border-left: 1px solid rgb(228, 228, 228);
}
.tickets__info-wrapper > div {
  display: flex;
  max-height: 40px;
  flex: 1 1 auto;
  flex-direction: row;
  justify-content: center;
}
.time {
  font-size: 32px;
  flex: 1 2 auto;
  padding-right: 20px;
  text-indent: 20px;
}
.info-transplan {
  display: flex;
  flex: 2 1 auto;
  align-items: center;
  justify-content: center;
  height: 28px;
  min-width: 130px;
  color: gray;
  border-bottom: 1px solid rgb(202, 202, 202);
}
.glyphicon-plane {
  padding-left: 20px;
  color: rgb(202, 202, 202);
  transform: rotate(90deg); 

}
.tickets__info-wrapper > div:last-child {
  display: flex;
  justify-content: center;
}
.tickets__info-wrapper > div:last-child > div {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  align-items: flex-start; 
}
.tickets__info-wrapper > div:last-child > div:last-child {
  display: flex;
  flex: 1 10 auto;
 }
.tickets__info-wrapper > div:last-child > div:first-child {
  display: flex;
  flex: 10 1 auto;
  text-indent: 30px;
}
.data {
  color: gray;
  text-indent: 30px;
}